.header {
    position: relative;
    padding: 25px 0 20px 30px;
    z-index: 2;
    margin-bottom: 100px;
}

.title {
    margin-bottom: 0;
    font-size: 45px;
    color: #fff;
}

.title span {
    display: block;
    font-size: 20px;
    color: #f9d74d;
}

.loginBlock {
    margin-top: 40px;
    color: #fff;
}

.loginLink {
    color: #fff;
}

.logout {
    cursor: pointer;
    text-decoration: underline;
}