.banner {
    position: absolute;
    width: 100%;
    opacity: 0.65;
    z-index: 1;
}

.banner::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(32, 30, 43, 0.75) 75%, rgba(32, 30, 43, 1) 100%);
}